
/*=====================
Dark start
==========================*/
body {
  &.dark {
    background-color: $dark-layout !important;
    transition: all 0.3s ease;
    color: $font-layout-5;
  }
}
.dark {
  .product-notification{
    background-color: $light-layout;
    .close{
      color: $light-border;
    }
  }
  h1{
    color:$white;
  }
  h2 {
    color:$font-layout-1 ;
  }
  h3 {
    color:$font-layout-2 ;
  }
  h4 {
    color:$font-layout-3 ;
  }
  h5 {
    color:$font-layout-4 ;
  }
  h6 {
    color:$font-layout-5 ;
  }

  p {
    color: $font-layout-5 ;
  }
  li {
    color: $font-layout-5 ;
  }
  a {
    color: $dark-link;
  }
  input,textarea {
    border-color: $dark-border !important;
    background-color: $light-layout;
    &::placeholder {
      color: $font-layout-5 !important;
    }
  }
  select {
    background-color: $dark-layout !important;
    color: $font-layout-5 !important;
    &:focus {
      background-color: $light-layout;
      color: $font-layout-5 !important;
    }
  }
  .layout-header3 .main-menu .menu-right .gift-block .gift-offer p{
    color: $white;
  }
  .gift-dropdown{
    &.dropdown-menu {
      border: 2px solid $dark-border;
      background-color: $dark-layout;
      .media {
        &+ .media {
          border-top: 1px solid $dark-border;
        }
      }
    }


  }
  .category-header-4{
    .sm-horizontal{
      background-color: $theme-color1;
    }
    .pixelstrap a{
      color: $white !important;
    }
  }
  label {
    color: $font-layout-1 !important;
  }
  .toggle-nav i{
    color: $font-layout-1;
  }
  .category-header {
    .navbar-menu {
      .category-left .input-block .input-box .big-deal-form .input-group select {
        background: url(../images/icon/drop.png) no-repeat scroll 20px 23px;
      }
    }
  }
  .mobile-setting-hover {
    .mobile-setting-show {
      background-color: $light-layout;
      border: 5px solid $body-back;
    }
  }
  .layout-header3 .main-menu .menu-right .input-block .input-box .big-deal-form .input-group select {
    background: url(../images/icon/drop.png) no-repeat scroll 20px 15px;
  }
  .layout-header2 .main-menu-block .input-block .input-box .big-deal-form .input-group select,
  
  .category-header-4 .navbar-menu .category-right .input-block .big-deal-form .input-group select{
    background: url(../images/icon/drop.png) no-repeat scroll 20px 23px;
    border-right: 1px solid $dark-border;
  }

  .layout-header3 .main-menu .menu-left .sm-nav-block .nav-slide li{
    border-color: $dark-border;
  }
  .sm-horizontal,.sm-vertical {
    .mobile-back {
      color: $font-layout-3;
      border-bottom-color: $dark-border;
    }
  }
  .tab-product-main .tab-prodcut-contain ul li a{
    color: $white;
  }
  .layout-5 .media-banner.media-banner-1 {
    background-color: $dark-layout;
  }
  .blog {
    .blog-contain{
      .blog-img{
        border: 1px solid $light-layout;
      }
      .blog-details-2{
        background-color: $light-layout;
      }
    }
  }
  // ganaral //
  .b-g-white {
    background-color: $light-layout !important;
  }
  .b-g-white1 {
    background-color:$light-layout !important;
  }
  .slick-prev,.slick-next {
    &:before {
      color: $font-layout-2 !important;
    }
  }

  .category-header-6{
    .pixelstrap a {
      color: #fff !important;
    }
  }
  .bg-light {
    background-color: $dark-layout !important;
  }

  .bg-white {
    background-color: $dark-layout !important;
  }

  // creative card //
  .creative-card {
    background-color: $light-layout;
  }

  // header //
  .top-header {
    .top-header-right {
      .language-block {
        .language-dropdown {
          .language-dropdown-open {
            background-color: $dark-layout;
            li {
              a {
                color: $font-layout-5;
              }
            }
          }
        }
        .curroncy-dropdown {
          .curroncy-dropdown-open {
            background-color: $dark-layout;
            li {
              a {
                color: $font-layout-5;
              }
            }
          }
        }
      }
    }
  }
  .category-header {
    .navbar-menu {
      .category-left {
        .nav-block {
          .nav-left {
            .nav-cat {
              background-color: $light-layout;
              li {
                a {
                  color: $font-layout-5;
                }
                img {
                  border-color: darken($theme-color1, 30%);
                }
              }
            }
            .navbar {
              background-color: $light-layout;
              h5 {
                color: $font-layout-4 !important;
              }
            }
          }
        }
        .input-block {
          .input-box {
            .big-deal-form {
              .input-group {
                span {
                  i {
                    color: $font-layout-5;
                  }
                }
                .input-group-prepend {
                  background-color: $light-layout;
                  select {
                    background-color: $light-layout;
                    color: $font-layout-5;
                  }
                  span {
                    background-color: $light-layout;
                  }
                }
                .form-control {
                  background-color: $light-layout;
                  border-color: $dark-border !important;
                  &::placeholder {
                    color: $font-layout-5;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .layout-header1 {
    background-color: $dark-layout;
    .main-menu-block {
      .menu-right {
        .icon-nav {
          ul {
            li {
              a {
                i {
                  color: $font-layout-5;
                }

              }
              span {
                color: $font-layout-5;
              }
            }
          }
        }
      }
    }
  }
  .layout-header2 {
    background-color:$dark-layout;
    .main-menu-block {
      .input-block {
        .input-box {
          .big-deal-form {
            .input-group {
              background-color: $dark-layout;
              .form-control {
                background-color: $dark-layout;
              }
              span {
                background-color: $dark-layout;
                i {
                  color: $font-layout-5;
                }
              }
            }
          }
        }
      }
      .cart-block {
        .cart  {
          i {
            color: $font-layout-5;
          }
        }
        .cart-item {
          h5 {
            color: $font-layout-5;
          }
        }
      }
    }
  }
  .category-header-2 {
    .navbar-menu {
      .category-left {
        .nav-block {
          .nav-left {
            .nav-cat {
              background-color: $dark-layout;
              li {
                img {
                  border-color: $dark-border ;
                }
                a {
                  color: $font-layout-5;
                }
              }
            }
          }
        }
      }
    }
  }
  .layout-header3 {
    .main-menu {
      .menu-left {
        .nav-block {
          .nav-left {
            .nav-cat {
              background-color: $light-layout;
              li {
                a {
                  color: $font-layout-5;
                }
              }
            }
          }
        }
      }
    }
  }
  .layout-header4{
    background-color: $dark-layout;
    .contact-block {
      .desc-nav-block  {
        .contact-item {
          color: $font-layout-4;
          span {
            color: $font-layout-4;
          }
        }
        .mobile-user {
          color: $font-layout-5;
        }
      }
    }
    .icon-block {
      ul {
        li {
          a {
            i {
              color: $font-layout-4;
            }
            .cart-item {
              color: $font-layout-4;
              span {
                color: $font-layout-5;
              }
            }
          }
        }
      }
    }
  }
  .category-header-4 {
    .navbar-menu {
      .category-left {
        .nav-block {
          .nav-left {
            .nav-cat {
              background-color: $light-layout;
              li {
                a {
                  color: $font-layout-5;
                }
              }
            }
          }
        }
      }
      .category-right {
        .input-block {
          .big-deal-form {
            background-color: $light-layout;
            .input-group {
              span {
                background-color: $light-layout;
                i {
                  color: $font-layout-5;
                }
              }
              .form-control {
                background-color: $light-layout;
                border-color: $dark-border !important;
              }
            }
          }
        }
      }
    }
  }
  .onhover-show-div {
    background-color: $light-layout;
    li {
      a {
        color: $font-layout-5;
      }
    }
  }
  .cart-hover-div {
    .cart-show-div {
      background-color: $light-layout;
      li {
        .media {
          .media-body {
            h6 {
              color: $font-layout-5;
            }

          }
        }
        .check-btn {
          a {
            color: $font-layout-4;
          }
        }
        .total {
          border-color: $dark-border;
          h6 {
            color: $font-layout-5;
          }
        }
      }
    }
  }

  // menu //
  .pixelstrap {
    ul {
      border-color: #4c4c4c;
      background-color: $light-layout;
      a,
      &:hover,
      &:active {
        color: $dark-link !important;
      }
    }
    a {
      color: $dark-link !important;
    }
    .link-section {
      h5 {
        color: $font-layout-4;
      }
    }
  }

  // brand panner //
  .brand-panel {
    .brand-panel-box {
      .brand-panel-contain {
        background-color: $light-layout;
        ul {
          li {
            a {
              color: $font-layout-5;
            }
          }
        }
      }
    }
  }



  // collection-banner //
  .collection-banner {
    .collection-banner-main{
      &:after {
        background-color: rgba($dark-layout,0.2);
      }
    }
  }

  .collection-wrapper  {
    .product-slick {
      background-color: $light-layout;
    }
  }
  .product-wrapper-grid {
    &.list-view {
      .product  {
        .product-box {
          background-color: $light-layout;
        }
      }
    }
  }
  .Jewellery-banner {
    h6 {
      color: $light-layout;
    }
    a {
      color: $light-layout;
    }
  }

  // blog //
  .blog {
    .blog-contain {
      .blog-details {
        background-color: $light-layout;
        p {
          color: $font-layout-5;
        }
      }
      h4 {
        color: $font-layout-3;
      }
    }
  }
  .blog-detail-page  {
    .comment-section {
      li {
        border-color: $dark-border;
      }
    }
  }

  // breadcrumb //
  .breadcrumb-main {
    background-color: $light-layout;
    .breadcrumb-contain {
      h2 {
        color: $font-layout-1;
      }
      ul {
        li {
          i {
            color: $font-layout-3;
          }
          a {
            color: $font-layout-5;
          }
        }
      }
    }
  }

  // category //
  .box-category {
    .box-category-contain {
      background-color: $light-layout;
      h4 {
        color: $font-layout-3;
      }
      &:hover {
        background: $gray ;
      }
    }

  }

  // media banner //
  .media-banner {
    background-color: $dark-layout;
    border-color: $dark-border;
    .media-banner-box {
      .media-heading {
        h5 {
          color: $font-layout-4;
        }
      }
      .media  {
        .media-body  {
          .media-contant  {
            p {
              color: $font-layout-2;
            }
            h6 {
              color: $font-layout-4;
            }
          }
        }
      }
    }
  }

  // discount-banner //
  .discount-banner {
    background-color: $light-layout;
    .discount-banner-contain {
      h1 {
        color: $font-layout-3;
      }
    }
  }

  // hote deal //
  .hot-deal {
    .hot-deal-contain1 {
      background-color: $light-layout;
      .hot-deal-heading {
        h5 {
          color: $font-layout-4;
        }
      }
      &.hot-deal-banner-1 {
        background-color: $dark-layout;
      }
      .hot-deal-subcontain {
        .hot-deal-center {
          h5 {
            color: $font-layout-5;
          }
          .timer {
            span {
              color: $font-layout-5;
            }
          }
        }
      }
    }
    .hot-deal-contain {
      background-color: $dark-layout;
      .hot-deal-center {
        .price {
          span {
            color: $font-layout-4;
          }
        }
        h5 {
          color: $font-layout-4;
        }
        .timer {
          span {
            background-color: $light-layout;
            color: $font-layout-5;
          }
        }
      }
      &.hot-deal-contain2 {
        .hot-deal-center {
          .timer {
            span {
              background-color: $dark-layout;
            }
          }
        }
      }
    }
    .slick-prev,.slick-next {
      &:before {
        color: $font-layout-1;
      }
    }
  }

  // tab product //
  .tab-product-main {
    background-color: $light-layout;
  }

  // product //
  .product {
    .product-box {
      .product-detail {
        &.detail-inline {
          background-color: $light-layout;
        }
        &.detail-center {
          background-color: $light-layout;
        }
        .detail-title {
          .detail-left {
            .price-title {
              color: $font-layout-5;
            }
          }
        }
      }
    }
  }

  // instagram //
  .instagram  {
    .insta-contant {
      border-color: $light-layout;
      &.insta-contant-inverse {
        border-color: $dark-layout;
      }
      .insta-sub-contant  {
        .insta-title {
          background-color: $light-layout;
          h4 {
            color: $font-layout-3;
          }
        }
      }
    }
    .insta-contant1  {
      .insta-sub-contant1  {
        .insta-title {
          background-color: $dark-layout;
          h4 {
            color: $font-layout-3;
          }
        }
      }
    }
    .insta-contant2 {
      border-color: $light-layout;
      .insta-title {
        background-color: $dark-layout;
        h4 {
          color: $font-layout-3;
        }
      }
    }
  }

  // title //
  .title1 {
    background-color: $light-layout;
    h4 {
      color: $font-layout-5;
    }
  }

  .title2 {
    background-color: $light-layout;
    h4 {
      color: $font-layout-5;
    }
  }
  .title3 {
    background-color: $dark-layout;
    h4 {
      color: $font-layout-5;
    }
  }
  .title4 {
    background-color: $light-layout;
    h4 {
      color: $font-layout-5;
    }
  }
  .title5 {
    background-color: $light-layout;
    h4 {
      color: $font-layout-5;
    }
  }
  .title6 {
    background-color: $light-layout;
    h4 {
      color: $font-layout-5;
    }
  }

  // newslatter //
  .newsletter {
    background-color: $light-layout;
    .news-leble {
      border-color: $dark-border;
      .news-text {
        color: $font-layout-3;
      }
    }
  }

  // footer //
  .app-link-block {
    background-color: $dark-layout;
    .app-link-bloc-contain {
      .app-item-group {
        .sosiyal-block  {
          ul {
            li {
              background-color: $light-layout;
              a {
                i {
                  color: $font-layout-5;
                }
              }
              &:hover {
                background-color: $dark-layout;
              }
            }
          }
          h6 {
            color: $font-layout-5;
          }
        }
      }
    }
  }
  .sub-footer {
    background-color: $light-layout;
    .footer-end  {
      p {
        color: $font-layout-5;
      }
    }
  }
  .footer-1 {
    background-color: $light-layout;
    .logo-contain {
      .logo-detail {
        &:before {
          background-color: $dark-border;
        }
      }
      p {
        color: $font-layout-5;
      }
    }
    .footer-link {
      border-color: $dark-border;
    }
  }
  .footer-2 {
    background-color: $light-layout;
    .footer-main-contian {
      border-color: $dark-border;
      .footer-right {
        border-color: $dark-border;
        .subscribe-section {
          border-color:$dark-border ;
          .subscribe-block {
            .subscrib-contant {
              .input-group {
                input {
                  background-color: $light-layout;
                }
                span {
                  border-color: $dark-border;
                }
              }
              h4 {
                color: $font-layout-5;
              }
            }
          }
        }
        .account-right  {
          .footer-box  {
            .footer-title {
              border-color: $dark-border;
            }
          }
        }
      }
      .footer-left {
        border-color: $dark-border;
        .footer-detail {
          p {
            color: $font-layout-5;
          }
        }
      }
    }
    .sub-footer {
      .sub-footer-contain  {
        p {
          color: $font-layout-5;
        }
      }
    }
  }
  .footer-3 {
    background-color: $dark-layout;
    .sosiyal-footer {
      border-color: $dark-border;
      .subscribe-section {
        .subscribe-block {
          .input-group {
            input {
              background-color: $dark-layout;
              border-color: $dark-border;
            }
            span {
              border-color: $dark-border;
            }
          }
          h4 {
            color: $font-layout-3;
          }
        }
      }
    }
    .sosiyal-block {
      .sosiyal {
        li {
          background-color: $light-layout;
          a {
            i {
              color: $font-layout-5;
            }
          }
        }
      }
    }
    .sub-footer {
      h6 {
        color: $font-layout-5;
      }
    }
  }
  .footer-4 {
    background-color: $dark-layout;
    .follow {
      background-color: $light-layout;
    }
    .sosiyal-block {
      ul {
        li {
          background-color: $dark-layout !important;
        }
      }
    }
    .logo-detail {
      p {
        color: $font-layout-4;
      }
    }
  }
  .footer-title {
    h5 {
      color: $font-layout-4 !important;
    }
  }
  .footer-contant {
    ul {
      li  {
        color: $font-layout-5 !important;
        a {
          color: $font-layout-5 !important;
        }
      }
    }
    p {
      color: $font-layout-5 !important;
    }
  }

  /*=====================
  inner pages
  ==========================*/

  // category  dark//
  .page-link {
    background-color: $dark-layout;
    border-color: $dark-border;
  }
  .collection-filter-block {
    background-color: $light-layout;
    border-color: $dark-border;
    .collapse-block-title {
      color: $font-layout-4;
      &::after {
        color: $font-layout-1;
      }
    }
    .collection-collapse-block-content {
      .collection-brand-filter {
        .collection-filter-checkbox {
          label {
            color: $font-layout-5;
          }
        }
      }
      .category-list {
        li {
          a {
            color: $font-layout-5;
          }
        }
      }
    }
    .product-service {
      .media {
        border-color:$dark-border;
        .media-body {
          h4 {
            color: $font-layout-4;
          }
        }
      }
    }
  }
  .top-banner-wrapper {
    .top-banner-content {
      h4 {
        color: $font-layout-3;
      }
      h5 {
        color: $font-layout-4;
      }
      p {
        color: $font-layout-3;
      }
    }
  }
  .collection-product-wrapper {
    .product-top-filter {
      border-color: $dark-border;

      .product-filter-content {
        .search-count {
          border-color: $dark-border;

          h5 {
            color: $font-layout-3;
          }
        }

        .collection-grid-view {
          border-color: $dark-border;
        }

        .collection-view {
          ul {
            li {
              i {
                color: $font-layout-3;
              }
            }
          }
        }

        .product-page-per-view {
          select {
            color: $font-layout-3;
            border-color: $dark-border;
          }
        }
      }

      .product-wrapper-grid {
        &.list-view {
          .product {
            .product-box {
              background-color: $light-layout;
            }
          }
        }
      }
    }
  }
  .theme-card {
    .offer-slider {
      .media {
        .media-body {
          h6 {
            color: $font-layout-5;
          }

          h4 {
            color: $font-layout-3;
          }
        }

        .popup-filter {
          .sidebar-popup {
            a {
              color: $font-layout-3;
            }
          }

          .collection-view {
            ul {
              li {
                color: $font-layout-3;
              }
            }
          }
        }
      }
    }
    h5 {
      &.title-border {
        border-color: $dark-border;
      }
    }
  }

  .product-pagination {
    border-color: $dark-border;
    .pagination {
      border-color: $dark-border;
      .page-item {
        &.active {
          a {
            background-color: $light-layout;
            transition: all 0.3s ease;
            border-color: $dark-border;
          }
        }

        a {
          color: $font-layout-3;
        }
      }
    }

    .product-search-count-bottom {
      border-color: $dark-border;

      h5 {
        color: $font-layout-3;
      }
    }
  }


  // product dark//
  .qty-box {
    .input-group {
      span {
        button {
          background-color: $light-layout !important;
        }
      }

      .form-control {
        background-color: $light-layout;
        color: $font-layout-5;
        border-color: $font-layout-5;
      }

      button {
        border-color: $dark-border;

        i {
