/*=====================
    Rtl start
==========================*/

body.rtl {
  direction: rtl;
}
.rtl {
  h1, h2, h3, h4, h5, h6, p {
    text-align: right;
  }
  .product-notification{
    .close{
      left: 13px;
    }
  }
  .slick-slider {
    direction: ltr;
  }
  .slick-slide {
    float: left;
  }
  .product-notification{
    .media{
      .mr-2{
        margin-right: 0 !important;
        margin-left: 0.5rem;  
      }
      .media-body{
        text-align: right;
      }
    }
  }
  ul {
    -webkit-padding-start: 0;
  }
  .pixelstrap ul{
    left: unset !important;
    right: unset;
  }
  .slider-layout-4 .slider-slide{
    margin-left: 0;
    margin-right: 275px;
  }
  .layout-4-collection>div:first-child {
    margin-left: 0;
    margin-right: 275px;
  }
  .mr-3 {
    margin-right: 0 !important;
    margin-left: 5px ;
  }
  .input-group-prepend{
    margin-right: 0;
  }
  .offset-md-3, .offset-lg-2, .offset-xl-3, .offset-lg-3, .offset-md-2 {
    margin:0 auto;
  }
  .title1, .title2, .title3, .title4 {
    h1, h2, h3, h4, h5, h6 {
      text-align: center;
    }
  }
  .setting-box {
    direction: ltr;
    h1, h2, h3, h4, h5, h6, p {
      text-align: left;
    }
  }
  .brand-logo {
    img {
      transform: scaleX(-1);
    }
  }
  [dir="rtl"] .slick-slide {
    float: left;
  }
  .effect-cls {
    &:before, &:after {
      right:0;
    }
  }
  .cart-section , .wishlist-section {
    .wishlist-buttons {
      text-align: left;
    }
  }
  .category-block {
    .category-details {
      h6 {
        text-align: center;
      }
    }
  }
  .offer-slider {
    .slick-slide {
      > div {
        direction: rtl;
      }
    }
  }
  .tools-parallax-product {
    .tools-description {
      .tools-form {
        .search-button {
          text-align: right;
        }
      }
    }
  }
  .typo-content {
    &.typo-buttons {
      text-align: right;
    }
  }
  .typography_section {
    .typography-box {
      .headings {
        text-align: right;
      }
      ul, ol, dl {
        text-align: right;
      }
      ol {
        padding-inline-start: 0px;
      }
      .typo-content {
        &.input_button {
          text-align: right;
        }
      }
    }
  }
  .absolute_banner {
    .collection-banner {
      .absolute-contain {
        h3, h4 {
          text-align: center;
        }
      }
    }
  }
  .absolute-product {
    .product-box {
      .product-detail {
        .rating,
        h4,
        h6,
        .color-variant {
          text-align: center;
        }
      }
    }
  }
  .product-box, .product-wrap {
    position: relative;
    transition: all 0.5s ease;
    vertical-align: middle;
    .img-wrapper {
      .front {
        text-align: right;
        left: unset;
        right: 0;
      }
      .back {
        transform: translateX(100px);
        text-align: right;
        left: unset;
        right: 0;
      }
    }
    &:hover {
      .img-wrapper {
        .back {
          transform: translateX(0);
        }
      }
    }
  }
  .box-product {
    .full-box {
      .theme-card {
        .offer-slider {
          .product-box2 {
            .media {
              padding-right: 0;
              padding-left: 15px;
              img {
                padding: 0 0 0 15px;
              }
            }
            .cart-bottom {
              text-align: right;
            }
          }
        }
      }
    }
  }
  .form_search {
    button {
      left: 5px;
      right: unset;
    }
  }
  .j-box {
    .product-box {
      .product-detail {
        padding-left: 0;
        padding-right: 15px;
      }
    }
  }
  .gym-product {
    .product-box {
      .product-detail {
        h6, h4 {
          text-align: center;
        }
      }
    }
  }
  .search-overlay {
    > div {
      .closebtn {
        right:unset;
        left:25px;
      }
    }
  }
  .pwd-page {
    h2 {
      text-align: center;
    }
  }
  .product-box, .product-wrap {
    .color-variant {
      -webkit-padding-start: 0;
    }
    .product-info {
      h4, h6 {
        text-align: center;
      }
    }
    .product-detail {
      .rating {
        text-align: right;
      }
      .color-variant {
        text-align: right;
        padding: 0;
        padding-top: 15px;
        li {
          &:first-child {
            margin-right: 0;
          }
        }
      }
    }
    .img-block {
      .label-wrapper {
        .label1 {
          border-bottom-right-radius: 25px;
          border-top-right-radius: 25px;
          border-bottom-left-radius: 0;
          border-top-left-radius: 0;
        }
        .label2 {
          border-bottom-left-radius: 25px;
          border-top-left-radius: 25px;
          border-bottom-right-radius: 0;
          border-top-right-radius: 0;
        }
      }
    }
    .img-wrapper {
      .label-block {
        .label3 {
          left:unset;
          right: 7px;
        }
        .label4 {
          left: 7px;
          right: unset;
        }
      }
    }
    .cart-detail {
      left: 20px;
      right: unset;
    }
    .cart-info {
      &.cart-wrap {
        left: 10px;
        right: unset;
      }
    }
    &:hover {
      .cart-wrap {
        button {
          animation: fadeInLeft 300ms ease-in-out;
        }
        a {
          &:nth-child(2) {
            i {
              animation: fadeInLeft 500ms ease-in-out;
            }
          }
          &:nth-child(3) {
            i {
              animation: fadeInLeft 700ms ease-in-out;
            }
          }
          &:nth-child(4) {
            i {
              animation: fadeInLeft 1000ms ease-in-out;
            }
          }
        }
      }
      .cart-detail {
        button {
          animation: fadeInLeft 300ms ease-in-out;
        }
        a {
          &:nth-child(2) {
            i {
              animation: fadeInLeft 500ms ease-in-out;
            }
          }
          &:nth-child(3) {
            i {
              animation: fadeInLeft 700ms ease-in-out;
            }
          }
          &:nth-child(4) {
            i {
              animation: fadeInLeft 1000ms ease-in-out;
            }
          }
        }
      }
    }
  }
  .subscribe {
    h4 {
      text-align: center;
      i {
        padding-left: 5px;
        padding-right: 0;
      }
    }
  }
  .service-block {
    svg {
      margin-left: 12px;
      margin-right: 0;
    }
    + .service-block {
      border-right: 1px solid lighten($black, 86.5%);
      border-left: none;
    }
  }
  .blog-details {
    h4, h6 {
      text-align: center;
    }
    a {
      p {
        text-align: center;
      }
    }
  }
  .instagram {
    h2 {
      text-align: center;
    }
  }
  .category-block {
    .category-details {
      h5 {
        text-align: center;
      }
    }
  }
  .category-bg {
    .contain-block {
      h6 {
        text-align: center;
      }
    }
  }
  .about-text {
    p {
      text-align: center;
    }
  }
  .full-banner {
    &.p-left {
      .banner-contain {
        float: right;
      }
    }
    &.p-right {
      .banner-contain {
        float: right;
      }
    }
    &.p-center {
      .banner-contain {
        left: 0;
        right: 0;
        margin: 0 auto;
        h3,h4 {
          text-align: center;
        }
        .btn-solid, .btn-outline {
          float: unset;
        }
      }
    }
    &.text-center {
      .banner-contain {
        h2, h3, h4 {
          text-align: center;
        }
      }
    }
    &.text-left {
      .banner-contain {
        h2, h3, h4 {
          text-align: right;
        }
      }
    }
    &.text-right {
      .banner-contain {
        h2, h3, h4 {
          text-align: right;
        }
      }
    }
  }
  .theme-card {
    .slick-prev {
      left: 0;
      right: unset;
    }
    .slick-next {
      right: unset;
      left: 25px;
    }
    .offer-slider {
      img {
        padding: 15px 0 15px 15px;
      }
      .media {
        .media-body {
          a {
            h6 {
              margin-left: 61px;
              margin-right: 0;
            }
          }
          .rating {
            text-align: right;
            i {
              padding-left: 5px;
            }
          }
        }
      }
    }
    &.card-border {
      h5 {
        padding-left: 0;
        padding-right: 50px;
      }
      .slick-prev {
        left: 30px;
      }
      .slick-next {
        left: 50px;
      }
      .offer-slider {
        img {
          padding: 15px 30px 15px 15px;
        }
        .slick-slide {
          > div {
            direction: rtl;
          }
        }
      }
    }
  }
  .background {
    .contain-bg {
      h4 {
        text-align: center;
      }
    }
  }
  .center-slider {
    &:hover {
      .slick-next {
        left: 20px;
        right: unset;
      }
      .slick-prev {
        left: unset;
        right: 20px;
      }
    }
  }
  .theme-tab {
    .tab-title {
      -webkit-padding-start: 0;
    }
    .tab-content {
      .product-tab {
        .tab-box {
          .product-box2 {
            img {
              padding: 15px;
            }
            .media {
              .media-body {
                .rating {
                  text-align: right;
                }
                a {
                  h6 {
                    margin-left: 35px;
                    margin-right: 0;
                  }
                }
                .color-variant {
                  text-align: right;
                  -webkit-padding-start: 0;
                }
              }
            }
          }
        }
      }
    }
  }
  .beauty-about {
    .about-text {
      p {
        text-align: right;
      }
    }
  }
  .timer {
    padding-left: 0;
    span {
      .padding-l {
        padding-left: 0;
        padding-right: 22px;
      }
    }
  }
  .category-m {
    .category-wrapper {
      h4 {
        text-align: center;
      }
      .category-link {
        -webkit-padding-start: 0;
      }
    }
  }
  .full-box {
    .theme-card {
      .offer-slider {
        .product-box2 {
          .media {
            .media-body {
              a {
                h6 {
                  margin-right: 0;
                  margin-left: 20px;
                }
              }
              .color-variant {
                text-align: right;
                -webkit-padding-start: 0;
              }
            }
          }
        }
      }
    }
  }
  .compare-page {
    .table-wrapper {
      .table {
        tbody {
          tr {
            th, td {
              text-align: right;
            }
          }
        }
        thead {
          .th-compare {
            td {
              text-align: right;
            }
            th {
              .remove-compare {
                float: right;
              }
            }
          }
        }
      }
    }
  }
  .product-wrap {
    .product-detail, .product-info {
      .rating, h4, h6, .color-variant {
        text-align: center;
      }
      .color-variant {
        li {
          margin: 0 2px;
        }
      }
    }
  }
  .main-menu {
    .menu-right {
      .icon-nav {
        .onhover-div {
          .show-div {
            &.shopping-cart {
              li {
                .media {
                  img {
                    &.mr-3 {
                      margin-right: unset !important;
                      margin-left: 1rem !important;
                    }
                  }
                }
                .close-circle {
                  right: unset;
                  left: 0;
                }
              }
            }
          }
        }
      }
    }
    .menu-left {
      .navbar  {
        i {
          float: unset;
        }
      }
    }
  }
  .sitemap_page {
    ul {
      text-align: right;
    }
  }
  .team {
    .slick-next {
      right: unset;
      left: 20px;
    }
    .slick-prev {
      right: unset;
      left: 0;
    }
    h4, h6 {
      text-align: center;
    }
  }
  .blog-page {
    .blog-sidebar {
      .theme-card {
        .recent-blog {
          text-align: right;
          li {
            .media {
              img {
                margin-right: 0;
                margin-left: 10px;
              }
            }
          }
        }
        .popular-blog {
          li {
            .blog-date {
              margin: 0 0 0 15px;
            }
          }
        }
      }
    }
    .blog-media {
      .blog-right {
        ul {
          text-align: right;
          li {
            + li {
              padding-right: 15px;
              margin-right: 15px;
              border-right: 1px solid $light-border;
              padding-left: 0;
              margin-left: 0;
              border-left: none;
            }
          }
        }
      }
    }
  }
  .blog-detail-page {
    .blog-detail {
      .post-social {
        text-align: right;
        li {
          + li {
            padding-right: 15px;
            margin-right: 15px;
            border-right: 1px solid $light-border;
            padding-left: 0;
            margin-left: 0;
            border-left: none;
          }
        }
      }
    }
    .blog-advance {
      ul {
        text-align: right;
      }
    }
    .comment-section {
      li {
        img {
          margin-right: 0;
          margin-left: 20px;
        }
      }
    }
    .blog-contact {
      .theme-form {
        label, .btn-solid, .btn-outline {
          float: right;
        }
      }
    }
  }
  .cart-section, .wishlist-section {
    tbody {
      tr {
        td {
          h2, p {
            text-align: center;
          }
        }
      }
    }
    tfoot {
      tr {
        td {
          &:first-child {
            padding-right: 0;
            padding-left: 10px;
            text-align: left;
          }
        }
      }
    }
    .cart-buttons {
      > div {
        &:last-child {
          text-align: left;
          padding-left: 59px;
          padding-right: 0;
        }
        &:first-child {
          .btn-solid, .btn-outline {
            float: right;
          }
        }
      }
    }
  }
  .collection-product-wrapper {
    .product-top-filter {
      .product-filter-content {
        .collection-view {
          ul {
            li{
              &:first-child {
                margin-right: 0;
                margin-left: 14px;
              }
            }
          }
        }
        .search-count {
          border-right: none;
          border-left: 1px solid $light-border;
          padding-right: 0;
          padding-left: 20px;
        }
        .product-page-per-view {
          select {
            border-right: none;
            border-left: 1px solid $light-border;
            padding: 20px;
            background: url(../images/dropdown.png) no-repeat 5%;
          }
        }
        .collection-grid-view {
          border-right: none;
          border-left: 1px solid $light-border;
        }
        .product-page-filter {
          select {
            padding: 20px;
            background: url(../images/dropdown.png) no-repeat 5%;
          }
        }
      }
      .popup-filter {
        .open-popup {
          left:unset;
          right:0;
        }
        .collection-grid-view, .product-page-per-view, .sidebar-popup, .search-count {
          border-right: none;
          border-left: 1px solid $light-border;
          select {
            border-right: none;
          }
        }
        .collection-view  {
          ul {
            float: right;
            li {
              &:first-child {
                margin-right: 0;
                margin-left: 14px;
              }
            }
          }
        }
        .product-page-per-view {
          select {
            background: url(../images/dropdown.png) no-repeat 5%;
          }
        }
        .product-page-filter {
          select {
            background: url(../images/dropdown.png) no-repeat 5%;
          }
        }
        .sidebar-popup {
          text-align: right;
          background: url(../images/dropdown.png) no-repeat 5%;
        }
      }
      .product-filter-content, .popup-filter {
        .collection-view {
          text-align: right;
        }
      }
    }
  }
  .metro-block {
    .product-box  {
      .product-imgbox  {
        .product-detail {
          text-align: center;
        }
      }
    }
  }
  .product-wrapper-grid {
    &.list-view {
      .product  {
        .product-box {
          .product-detail {
            &.detail-center {
              padding-right: 25px;
              padding-left: 0;
            }
          }
        }
      }
    }
  }
  .qty-box {
    .input-group {
      span {
        button {
          transform: rotate(180deg);
        }
      }
    }
  }
  .product-pagination {
    .product-search-count-bottom {
      padding-right: 0;
      padding-left: 15px;
    }
    .pagination {
      .page-item {
        a {
          i {
            transform: rotate(180deg);
          }
        }
      }
    }
