          }
        }
      }
    }
    @media (max-width: 420px) {

      .full-box {
        .center-slider {
          .offer-slider {
            .product-box {
              .product-detail {
                .rating, h4, h6, .color-variant {
                  text-align: right !important;
                }
              }
            }
          }
        }
      }

    }
    @media (max-width: 360px) {
      .product-right {
        .product-icon {
          .product-social {
            li {
              padding-left: 7px;
            }
          }
        }
      }
      .banner-timer {
        .timer {
          padding-left: 0;
          padding-right: 20px;
          span {
            width: 50px;
          }
        }
      }

    }

  }
