/*=====================
    1.General CSS start
==========================*/
body {
  font-family: $font_0,$font_1;
  font-size: 14px;
  background-color: $white;
  color: $body-text;
}
  h1 {
    font-weight:700;
    margin-bottom: 0;
    font-size: calc(30px + (72 - 30) * ((100vw - 320px) / (1920 - 320)));
  }
  h2 {
    font-weight: 400;
    margin-bottom: 0;
    font-size: calc(20px + (36 - 20) * ((100vw - 320px) / (1920 - 320)));
  }
  h3 {
    font-weight: 700;
    margin-bottom: 0;
    font-size: calc(18px + (30 - 18) * ((100vw - 320px) / (1920 - 320)));
  }
  h4 {
    font-weight: 700;
    margin-bottom: 0;
    font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
  }
  h5 {
    font-weight: 700;
    margin-bottom: 0;
    font-size: 16px;
  }
  h6 {
    font-size: 14px;
    margin-bottom: 0;
    font-weight: 400;
  }
  label {
    color: $font-color;
  }
  textarea,input {
    letter-spacing: 0.05em;
  }
  ul {
    padding-left: 0;
    margin-bottom: 0;
  }
  ::selection{
    background-color: $theme-color1;
    color: $theme-color2;
  }
  .small-section {
    padding-top: 35px;
    padding-bottom: 35px;
  }

  li {
    display: inline-block;
  }
  .o-hidden{
    overflow: hidden;
  }
  p {
    font-size: 14px;
    line-height: 1.4;
    margin-bottom: 0;
    letter-spacing: 0.05em;
  }

  a {
    transition: 0.5s ease;
    &:hover {
      text-decoration: none;
      transition: 0.5s ease;
    }
    &:focus {
      outline: none;
    }
  }
  button:focus {
    outline: none;
  }
  .alert{
    border-radius: 0;
    background-color: lighten(#5cb85c , 60%);
    border: 1px solid #5cb85c;
    span{
      color: #5cb85c;
    }
    .progress{
      margin-top: 5px;
      Height: 10px;
    }
    .progress-bar{
      background-color: #5cb85c;
      Height: 10px;
    }
  }
  .section-pt-space {
    padding-top: 15px;
  }
  .section-pb-space {
    padding-bottom: 15px;
  }
  .section-py-space {
    padding: 15px 0;
  }
  .section-big-pt-space {
    padding-top: 50px;
  }
  .section-big-pb-space {
    padding-bottom: 50px;
  }
  .section-big-py-space {
    padding: 50px 0;
  }
  .section-mb-space {
    margin-bottom: 15px;
  }
  .section-my-space {
    margin: 15px 0;
  }
  .mb--5{
    margin-bottom: -5px;
  }
  .mb--10{
    margin-bottom: -10px;
  }

  .mt--5 {
    margin-top: -5px;
  }
  .section-mt-space {
    margin-top: 15px;
  }
  .section-big-mt-space {
    margin-top: 50px;
  }
  .section-big-mb-space {
    margin-bottom: 50px;
  }
  .gutter-15{
    .col-sm-12,.col-sm-6,.col-md-12{
      padding-right: 7.5px;
      padding-left: 7.5px;
    }
    .masonary-banner-block2{
      .col-sm-6{
        padding-left: 15px;
      }
    }
  }
  .bg-light{
    background-color: $bg-light !important;
  }
  .no-arrow {
    .slick-next,.slick-prev{
      display: none !important;
    }
  }
  .timer {
    span {
      display: inline-grid;
      background-color: $white;
      padding:  12px  0 18px;
      text-align: center;
      font-size: 24px;
      font-weight: 700;
      color: $black;
      margin:0 8px;
      min-width: 70px;
      &:first-child {
        margin-left: 0;
      }
      &:last-child {
        margin-right: 0;
      }
      .padding-l {
        display: none;
      }
      .timer-cal {
        font-size: 12px;
        color: $icon-color;
      }
      >span {
        color: $light-gray;
        font-weight: 400;
        font-size: 14px;
        padding: 0;
        margin-left: 0;
        text-transform: capitalize;
      }
    }
    p {
      margin-bottom: 0;
    }
  }
  .txt-default{
    color: $theme-color1;
  }
  .bg-light0 {
    background-color: $pink;
  }
  .bg-light1 {
    background-color:$blue;
  }
  .bg-light2{
    background-color: $gray;
  }
  .b-g-white {
    background-color: $white !important;
  }
  .bg-silver {
    background-color: $hot-deal-1 !important;
  }
  .color-variant {
    li {
      margin-right: 5px;
      border-radius: 50%;
    }
  }

// image ratio //
.ratio_40{
  .bg-size {
    &:before {
      padding-top: 40%;
      content: "";
      display: block;
    }
  }
}

.ratio_45{
  .bg-size {
    &:before {
      padding-top: 45%;
      content: "";
      display: block;
    }
  }
}
.ratio2_1{
  .bg-size {
    &:before {
      padding-top: 50%;
      content: "";
      display: block;
    }
  }
}
.ratio2_3 {
  .bg-size {
    &:before {
      padding-top: 60%;
      content: "";
      display: block;
    }
  }
}
.ratio3_2{
  .bg-size {
    &:before {
      padding-top: 66.66%;
      content: "";
      display: block;
    }
  }
}
.ratio_landscape{
  .bg-size {
    &:before {
      padding-top: 75%;
      content: "";
      display: block;
    }
  }
}
.ratio_square{
  .bg-size {
    &:before {
      padding-top: 100%;
      content: "";
      display: block;
    }
  }
}
.ratio_asos{
  .bg-size {
    &:before {
      padding-top: 127.7777778%;
      content: "";
      display: block;
    }
  }
}
.ratio_portrait{
  .bg-size {
    &:before {
      padding-top: 150%;
      content: "";
      display: block;
    }
  }
}
.ratio1_2{
  .bg-size {
    &:before {
      padding-top: 200%;
      content: "";
      display: block;
    }
  }
}
.b-top {
  background-position: top !important;
}
.b-bottom {
  background-position: bottom !important;
}
.b-center {
  background-position: center !important;
}
.b_size_content {
  background-size: contain !important;
  background-repeat: no-repeat;
}
.light-layout {
  background-color:$gray1;
}
.b-g-white {
  background-color:$white;
}
.b-g-white1 {
  background-color:$white !important;
}


//1.1.Button CSS start
button {
  cursor: pointer;
}
.btn-rounded {
  font-size: 14px;
  padding:18px 50px;
  font-weight: 700;
  color:$white !important;
  background-color: $theme-color1;
  border-radius: 25px;
  position: relative;
  transition: all 0.3s;
  line-height: 1;
  display: inline-block;
  letter-spacing: 0.05em;
  &.btn-outline {
    background-color: $white ;
    border: 2px solid $theme-color1;
    color: $dark !important;
    &:hover {
      background-color: $theme-color1 ;
      color: $white !important;
    }
  }
  &:hover {
    background-color: $black;
  }
}
.btn-white {
  font-size: 14px;
  padding:16px 45px;
  font-weight: 700;
  color:$dark !important;
  background-color: $white;
  border-radius: 25px;
  position: relative;
  transition: all 0.3s;
  display: inline-block;
  line-height: 1;
  letter-spacing: 0.05em;
  &.btn-outline {
    background-color:transparent;
    border: 1px solid $theme-color1;
    &:hover {
      background-color: $theme-color1;
    }
  }
  &:hover {
    background-color:$black;
    color: $white !important;
  }
}
.btn-normal {
  font-size: 14px;
  padding:18px 45px;
  font-weight: 700;
  color:$white !important;
  background-color: $theme-color1;
  position: relative;
  transition: all 0.3s;
  display: inline-block;
  line-height: 1;
  border-radius: 0;
  letter-spacing: 0.05em;
  &.btn-outline {
    background-color: $white ;
    border: 2px solid $theme-color1;
    color: $dark !important;
    &:hover {
      background-color: $theme-color1 ;
      color: $white !important;
    }
  }
  &:hover {
    background-color: $black;
  }
}

/*=====================
  2.Services CSS start
==========================*/
.services{
  background-color: $theme-color2;
  &.services-inverse {
    background-color: $theme-color1;
  }
  .service-block {
    >div {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 23px 0;
    }
    .media {
      align-items: center;
      svg {
        width: 30px;
        height: 30px;
        fill:$white;
        margin-right: 12px;
        transition: 0.5s linear;
      }
      h5 {
        color: $white;
        text-transform: uppercase;
        line-height: 1;
        letter-spacing: 0.05em;
      }
      &:hover {
        svg {
          transform: scale(1.2);
        }
      }
    }
  }
  &.bg-light {
    .service-block  {
      .media {
        svg {
          fill: $theme-color1;
        }
        h5 {
          color: $dark;
        }
      }
    }
  }
}

/*=====================
   3.Instagram CSS start
==========================*/
.instagram{
  .insta-contant {
    border: 15px solid $white;
    line-height: 0;
    position: relative;
    .instagram-box{
      position: relative;
      overflow: hidden;
      .insta-cover{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color:rgba($theme-color1,.7);
        justify-content: center;
        align-items: center;
        display: flex;
        transition: all 0.5s ease;
        opacity: 0;
        i {
          font-size: 20px;
          color:$white;
          transition: all 0.5s ease;
          transform: rotate(-217deg);
        }
      }
      &:hover {
        .insta-cover {
          opacity: 1;
          i{
            transform: scale(3) rotate(-360deg);
          }
        }
      }
    }
    .insta-sub-contant {
      position: absolute;
      margin: 0;
      top: 50%;
      left: 50%;
      margin-right: -50%;
      transform: translate(-50%, -50%) ;
      .insta-title {
        background-color: $white;
        padding: 20px 30px;
        width: auto;
        h4 {
          color: $dark;
          span {
            margin-right: 10px;
          }
        }
      }
    }
    &.insta-contant-inverse {
      border-color: $hot-deal-1 ;
    }
  }
  .insta-contant1 {
    line-height: 0;
    .instagram-box{
      position: relative;
      overflow: hidden;
      .insta-cover{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color:rgba($theme-color1,.7);
        justify-content: center;
        align-items: center;
        display: flex;
        transition: all 0.5s ease;
        opacity: 0;
        i {
          font-size: 20px;
          color:$white;
          transition: all 0.5s ease;
        }
      }
      &:hover {
        .insta-cover {
          opacity: 1;
          i{
            transform: scale(3) rotate(-360deg);
          }
        }
      }
    }
    .insta-sub-contant1 {
      margin: 0;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-right: -50%;
      transform: translate(-50%, -50%) ;
      .insta-title {
        background-color: $white;
        padding: 18px 80px;
        text-align: center;
        h4 {
          color: $dark;
          span {
            margin-right: 10px;
          }
        }
      }
    }
  }
  .insta-contant2 {
    border: 15px solid $white;
    position: relative;
    line-height: 0;
    .instagram-box{
      position: relative;
      overflow: hidden;
      .insta-cover{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color:rgba($theme-color1,.7);
        justify-content: center;
        align-items: center;
        display: flex;
        transition: all 0.5s ease;
        opacity: 0;
        i {
          font-size: 20px;
          color:$white;
          transition: all 0.5s ease;
        }

      }
      &:hover {
        .insta-cover {
          opacity: 1;
          i{
            transform: scale(3) rotate(-360deg);
          }
        }
      }
    }
    .insta-sub-contant2 {
      margin: 0;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-right: -50%;
      transform: translate(-50%, -50%);
      .insta-title {
        background-color: $white;
        padding: 12px 22px;
        width: auto;
        text-align: center;
        h4 {
