/*=====================
   4. Brand panel CSS start
==========================*/
.brand-panel {
  .brand-panel-box {
    background-color: $white;
    .brand-panel-contain {
      padding: 30px 0;
      display: flex;
      justify-content: center;
      text-align: center;
      ul {
        li {
          padding: 0 10px;
          text-transform: uppercase;
          a {
            color: $icon-color;
            letter-spacing: 0.05em;
          }
          &:first-child {
            a {
              color: $theme-color1;
              font-weight: 700;
            }
          }
          &:hover {
            color: $theme-color1;
            cursor: pointer;
          }
        }
      }
    }
  }
}

/*=====================
   5. Testimonial CSS start
==========================*/
.testimonial {
  background-color: $theme-color2;
  &.testimonial-inverse {
    background: $theme-color1;
  }
  .testimonial-contain {
    padding: 50px 0;
    max-width: 840px;
    margin: 0 auto;
    .media {
      .testimonial-img{
        width: 130px;
        height: 130px;
        border: 2px solid $white;
        padding: 5px;
        border-radius: 50%;
      }
      .media-body {
        color: $white;
        align-items: center;
        margin: 20px 0 0 40px;
        h5 {
          text-transform: uppercase;
          margin-bottom: 5px;
          line-height: 1;
          letter-spacing: 0.8px;
        }
        p{
          letter-spacing: 1px;
          line-height: 1.6;
        }
      }
    }
  }
}


/*=====================
   6. Offer-banner CSS start
==========================*/
.offer-banner {
  position: relative;
  overflow: hidden;
  .banner-contain {
    position: absolute;
    top:0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    h5 {
      font-size: 18px;
      color: $white;
      font-family: $font-2,$font-1;
      letter-spacing: 0.05em;
      font-weight:400;
      margin-top: 25px;
    }
    .discount-offer {
      display: flex;
      align-items: center;
      h1 {
        color: $white;
        line-height: 1;
        font-style: italic;
      }
      sup {
        font-size: 25px;
        color: $white;
        line-height: 1;
        font-style: italic;
        font-weight: 700;
      }
    }

  }
}

/*=====================
   7. Deal banner CSS start
==========================*/
.deal-banner {
  background-color:$theme-color1;
  padding: 40px 0;
  .deal-banner-containe {
    h2 {
      font-size: calc(14px + (24 - 14) * ((100vw - 320px) / (1920 - 320)));
      text-transform: uppercase;
      color: $white;
      font-weight: 400;
      line-height: 1;
      margin-bottom: 5px;
    }
    h1 {
      font-size: calc(16px + (40 - 16) * ((100vw - 320px) / (1920 - 320)));
      font-weight: 700;
      color: $white;
      text-transform: uppercase;
      line-height: 1;
      letter-spacing: 0.5px;
    }
    .deal-btn {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-end;
    }
  }
  &.deal-banner-inverse {
    background-color: $theme-color2;
  }
}

/*=====================
   8. Discount banner CSS start
==========================*/
.discount-banner {
  background-color: $white;
  .discount-banner-contain {
    padding: 47px 0;
    text-align: center;
    h2 {
      font-size: calc(14px + (24 - 14) * ((100vw - 320px) / (1920 - 320)));
      text-transform: capitalize;
      margin-bottom: 3px;
      margin-top: -5px;
    }
    h1 {
      font-size: calc(18px + (42 - 18) * ((100vw - 320px) / (1920 - 320)));
      font-weight: normal;
      text-transform: uppercase;
      margin-bottom: 10px;
      color: $dark;
      span {
        color: $theme-color1;
        +span {
          color:$theme-color2;
          font-weight: 700;
        }
      }
    }
    .rounded-contain {
      border: 2px dashed $theme-color2;
      border-radius: 50px;
      padding: 5px;
      .rounded-subcontain {
        background-color: $theme-color1;
        color: $white;
        text-transform: uppercase;
        padding: 18px 0;
        font-weight: 600;
        font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
        letter-spacing: .08em;
        line-height: 1;
        border-radius: 50px;
      }
      &.rounded-inverse {
        border: 2px dashed $theme-color1;
        .rounded-subcontain {
          background-color: $theme-color2;
        }
      }
    }
  }
}

/*============================
   9. Contact banner CSS start
==============================*/
.contact-banner {
  background-color: $theme-color1;
  padding: 20px 0;
  &.contact-banner-inverse {
    background-color: $theme-color2;
  }
  .contact-banner-contain {
    display: flex;
    justify-content: center;
    align-items: center;
    color: $white;
    .contact-banner-img {
      img {
        margin-right: 25px;
      }
    }
    h3{
      font-size: 18px;
      font-weight: bold;
      margin-right: 25px;
      line-height: 1;
    }
    h2 {
      font-size: calc(20px + (36 - 20) * ((100vw - 320px) / (1920 - 320)));
      font-weight: 700;
    }
  }
}

/*=====================
   10. Rounded category CSS start
==========================*/
.rounded-category {
  background-color: $theme-color2;
  padding: 50px 0 40px;
  &.rounded-category-inverse {
    background-color: $theme-color1;
    .category-contain {
      &:hover {
        .img-wrapper {
          border: 1px solid $theme-color2;
        }
        .btn-rounded {
          background-color: $theme-color2;
        }
      }
    }
  }
  .category-contain {
    text-align: center;
    .img-wrapper {
      border: 1px solid $white;
      padding: 5px;
      width: 110px;
      height: 110px;
      border-radius: 50%;
      margin: 0 auto;
      transition: 0.5s ease;
      img  {
        border-radius: 100%;
      }
    }
    .btn-rounded {
      padding: 10px 25px;
      font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
      display: inline-block;
      margin-top:15px;
      background-color: transparent;
      text-transform: uppercase;
    }
    &:hover {
      .img-wrapper {
        border: 1px solid $theme-color1;
      }
      .btn-rounded {
        background-color: $theme-color1;
      }
    }

  }
  &.vagitable-category {
    .category-contain {
      .img-wrapper {
        background-color: $category-back;
        padding: calc(15px + (20 - 15) * ((100vw - 320px) / (1920 - 320)));
      }
    }
  }
}

/*=====================
   11. Box category CSS start
==========================*/
.box-category {
  .box-category-contain {
    padding: 30px 0;
    background-color: $white;
    text-align: center;
    margin-left: 15px;
    position: relative;
    transition: all 0.3s;
    color: $dark;
    text-transform: uppercase;
    h4 {
      line-height: 1;
      letter-spacing: 1px;
    }
    &:after{
      background: $theme-color1;
      content: '';
      position: absolute;
      z-index: -1;
      width: 100%;
      height:0;
      right: 0;
      top: 0;
      transition: all 0.3s ;
    }
    &:hover {
      color: $white;
      background:transparent;
      &:after {
        height: 100%;
      }
    }
  }
}

/*=====================
    12.Blog CSS start
==========================*/
.blog {
  .blog-contain {
    margin-right: 15px;
    position: relative;
    .blog-img {
      overflow: hidden;
      border: 1px solid $white;
      img {
        transition: all 0.5s ease;
      }
    }
    .blog-details{
      background-color: $white;
      padding: 20px;
      h4 {
        color: $dark;
        margin-bottom: 13px;
        font-size: calc(17px + (18 - 17) * ((100vw - 320px) / (1920 - 320)));
        letter-spacing: 1px;
        font-weight: 600;
      }
      p {
        color: $gray-7;
        text-transform: capitalize;
        margin-bottom: 5px;
        letter-spacing: 0.5px;
      }
      span {
        a {
          color: $theme-color1;
          text-transform: capitalize;
          font-weight: 700;
          letter-spacing: 0.03em;
          line-height: 1;
