      position: relative;
      overflow: hidden;
      background-color: $white;
      img{
        margin: 0 auto;
      }
      .product-front {
        left: 0;
        top:0;
        transition: all 0.5s ease;
      }
      .product-back {
        position: absolute;
        top: 0;
        left: 0;
        transform:translateX(-100%);
        transition: all 0.5s ease;
      }
      .on-sale {
        background-color: $theme-color2;
        color: $white;
        text-transform: uppercase;
        position: absolute;
        top: 0;
        right:0;
        padding:0 6px;
        font-size: calc(10px + (12 - 10) * ((100vw - 320px) / (1920 - 320)));
      }
      .new-label {
        position: absolute;
        top: 0;
        left:0;
        padding: 5px;
        font-weight: 600;
        letter-spacing: 1px;
        div {
          color: $white;
          text-transform: uppercase;
          transform: rotate(-45deg);
          width:fit-content;
          font-size: calc(10px + (14 - 10) * ((100vw - 320px) / (1920 - 320)));
          margin-top: 3px;
        }
        &::before {
          width: 0;
          height: 0;
          border-top: 60px solid $theme-color1;
          border-right: 60px solid transparent;
          content: '';
          position: absolute;
          top: 0 ;
          left: 0 ;
        }
      }
      .product-icon {
