        height: 50px;
        border-radius: 50%;
        background-color: $theme-color1;
        color: $white;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 0.5s ease;
        text-transform: uppercase;
        font-weight: 700;
        font-size: calc(10px + (14 - 10) * ((100vw - 320px) / (1920 - 320)));
      }
      .on-sale2 {
        color: $theme-color2;
        text-transform: uppercase;
        position: absolute;
        top:40px;
