        transform:translateX(0);
      }
      .product-icon {
       opacity: 1;
        button {
          animation: fadeInRight 300ms ease-in-out;
        }
        a {
          &:nth-child(2) i{
            animation: fadeInRight 500ms ease-in-out;
          }
          &:nth-child(3) i{
            animation: fadeInRight 700ms ease-in-out;
          }
          &:nth-child(4) i{
            animation: fadeInRight 1000ms ease-in-out;
          }
