            }
            &:nth-child(3) i{
              animation: fadeInUp 700ms ease-in-out;
            }
            &:nth-child(4) i{
              animation: fadeInUp 1000ms ease-in-out;
            }
          }
        }
        &.icon-center {
          transform: scale(1);
          button {
            animation: none;
          }
          a {
            &:nth-child(2) i{
              animation: none;
            }
          }
