        overflow: hidden;
        padding: calc(10px + (15 - 10) * ((100vw - 320px) / (1920 - 320)));
        background-color: $white;
        transition: all 0.5s ease;
        .detail-title {
          transition: all 0.5s ease;
          display: block;
          .detail-left {
            width: 100%;
          }
          .detail-right {
