            margin-right: 10px;
            line-height: 1;
            &:last-child {
              color: $light-gray;
              text-decoration: line-through;
            }
          }
        }
      }
    }
  }

  .slick-prev  {
    &:before {
      color: $gray-2;
      opacity: 1;
      font: normal normal normal 30px/1 FontAwesome;
      content: '\f104';
    }
    top:  35px;
    right: 50px;
    left: unset;
    z-index: 9;
  }
  .slick-next {
    &:before {
      color: $gray-2;
      opacity: 1;
      content: '\f105';
      font: normal normal normal 30px/1 FontAwesome;
    }
    top:  35px;
    right: 25px;
    z-index: 9;
  }
}
