          color: $theme-color2;
          padding-bottom: 27px;
          text-transform: uppercase;
        }
        h2 {
          font-size: 24px;
          color: $dark;
          padding-bottom: 25px;
        }
        .btn-rounded {
          text-transform: capitalize;
          &:hover {
            background-color: $theme-color2;
          }
        }
      }
    }
  }
}
