      }
      ::-webkit-scrollbar-thumb {
        background:$dark;
      }
      ::-webkit-scrollbar-thumb:hover {
        background: $dark;
      }
      .sidebar-popup {
        position: relative;
        cursor: pointer;
        width: 20%;
        background: url(../images/dropdown.png) no-repeat 95%;
