.theme_checkbox {
  label {
    display: block;
    position: relative;
    padding-left: 30px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 14px;
    user-select: none;
    text-transform: capitalize;
    color: $font-color;
    input {
      position: absolute;
      opacity: 0;
      cursor: pointer;
      height: 0;
      width: 0;
      &:checked {
        ~ .checkmark {
          &:after {
            display: block;
          }
        }
      }
    }
    .checkmark {
      position: absolute;
      top: 0;
      left: 0;
      height: 20px;
      width: 20px;
      background-color: $white;
      border: 1px solid $light-border;
      &:after {
        content: "";
        position: absolute;
        display: none;
        left: 7px;
        top: 3px;
        width: 5px;
        height: 10px;
        border: solid black;
        border-width: 0 2px 2px 0;
        transform: rotate(45deg);
      }
    }
  }
}
.theme-card {
  position: relative;
  background-color: $white;
  &.center-align {
    display: flex;
    height: 100%;
    align-items: center;
    .offer-slider {
      .product-box2 {
        .media {
          .media-body {
            margin-top: 0;
          }
        }
      }
    }
  }
  &.card-border {
    border: 1px solid $light-border;
    h5 {
      padding-top: 20px;
      padding-bottom: 20px;
      padding-left: 30px;
      margin-top: 0;
    }
    .slick-prev, .slick-next {
      top:-40px;
    }
    .slick-next {
      right: 30px;
    }
    .slick-prev {
      right: 50px;
    }
    .offer-slider {
      padding-top: 0;
      img {
        padding: 15px ;
      }
      .media {
        .media-body {
          a {
            h6 {
              margin-right:0;
            }
          }
        }
      }
    }
  }
  h5 {
    font-weight: 700;
    text-transform: uppercase;
    padding-bottom: 10px;
    color: $font-color;
    letter-spacing: 0.05em;
    &.title-border {
      border-bottom: 1px solid $light-border;
      font-size: calc(16px + (20 - 16) * ((100vw - 320px) / (1920 - 320)));
    }
  }
  .slick-prev, .slick-next {
    top:-28px;
    &:before {
      font: normal normal normal 30px/1 $font-awesome;
      color: $font-color;
      opacity:0.6;
    }
  }
  .slick-next {
    right: 0;
    &:before {
      content: "\f105";
    }
  }
  .slick-prev {
    left: unset;
    right: 25px;
    &:before {
      content: "\f104";
    }
  }
  .offer-slider {
    img {
      height: 140px;
      padding: 15px 15px 0 0;
    }
    > div {
      .media {
        &:last-child {
          .media-body {
            margin-top: 15px;
          }
          img {
            margin-bottom: -15px;
          }
        }
      }
    }
    .media {
      .media-body {
        .rating {
          margin-top: 0;
          i {
            padding-right: 3px;
            &:nth-child(-n + 4){
              color: $rating-star;
            }
            &:last-child {
              color:$light-border;
            }
          }
        }
        a {
          h6 {
            margin-right: 61px;
            margin-top: 5px;
            font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
            margin-bottom: 0;
            color: $icon-color;
          }
        }
        h4 {
          margin-top: 10px;
          font-weight: 700;
          color: $font-color;
          margin-bottom: 0;
        }
      }
    }
  }
}
