// product page //
.product-slick, .rtl-product-slick, .product-right-slick, .rtl-product-right-slick {
  img {
    width: 100%;
  }
  .slick-prev, .slick-next {
    opacity: 0;
    transform: scale(1);
    transition: all 0.5s ease;
    margin-top: -10px;
    &:before {
      font: normal normal normal 14px/1 $font-awesome;
      opacity: 1;
      color: black;
      background-color: white;
      border-radius: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
  .slick-next {
    right:1px;
    &:before {
      content: "\f105";
    }
  }
  .slick-prev {
    left: 1px;
    z-index: 1;
    &:before {
      content: "\f104";
    }
  }
  .slick-slide {
    > div {
      display: flex;
      margin-bottom: 15px;
    }
  }
  &:hover {
    .slick-prev, .slick-next {
      opacity: 1;
      transform: scale(1.05);
      transition: all 0.5s ease;
      &:before {
        opacity: 1;
      }
    }
    .slick-next {
      right:20px;
    }
    .slick-prev {
      left: 20px;
    }
  }
}
.slider-nav, .rtl-slider-nav, .slider-right-nav, .rtl-slider-right-nav {
  .slick-slide {
    > div {
      display: flex;
      margin: 15px 15px 0 15px;
      border: 1px solid $white1;
      transition: all 0.5s ease;
    }
    &.slick-current {
      > div {
        border-color: $theme-color1;
        transition: all 0.5s ease;
      }
    }
    &.slick-active {
      transition: all 0.5s ease;
    }
  }
}
.slider-right-nav, .rtl-slider-right-nav {
  .slick-slide {
    &:first-child {
      > div {
        margin-top: 0;
      }
    }
  }
}
