        text-transform: uppercase;
      }
      h4 {
        color:$white;
      }
      h3 {
        color: $white;
        margin-top: 10px;
      }
      .color {
        color: $theme-color1;
        line-height: 1;
      }
    }
  }
  &.parallax{
    background-attachment: fixed;

  }
  &.p-left {
    .banner-contain{
      float: left;
    }
  }
  &.p-right {
    .banner-contain{
      float: right;
    }
  }
  &.p-center {
