    .btn-solid, .btn-outline {
      margin-top: 20px;
    }
    h2 {
      font-size: calc(55px + (100 - 55) * ((100vw - 320px) / (1920 - 320)));
      font-weight: 700;
      color: $theme-color1;
      text-transform: uppercase;
      margin-top: -15px;
    }
    h3 {
      font-size: calc(36px + (60 - 36) * ((100vw - 320px) / (1920 - 320)));
      color: $font-color;
      text-transform: uppercase;
      font-weight: 700;
    }
    h4 {
      font-size: calc(18px + (24 - 18) * ((100vw - 320px) / (1920 - 320)));
