      }
      .close-circle {
        position: absolute;
        top: 10px;
        right: 10px;
        i {
          color: $dark;
          font-size: 16px;
          transition: all 0.5s ease;
          &:hover {
            color: $black;
            transition: all 0.5s ease;
          }
