    height: 218px;
    width: 100%;
    .cart_media {
      display: flex;
      width: 100%;
      .cart_product {
        display: flex;
        width: 75%;
        overflow-y: auto;
        li {
          min-width: 245px;
          max-width: 225px;
          margin-right: -1px;
          padding-bottom: 0;
          border: 1px solid #ddd;
        }
      }
      .cart_total {
        width: 25%;
      }
    }
    .cart-inner {
      height: 218px;
      .cart_top {
        margin-bottom: 0;
        background-color: $gray1;
        padding: 15px 44px;
        border-bottom: none;
      }
      .cart_media {
        padding: 0;
        .cart_product, .cart_total {
          padding: 30px 44px;
        }
      }
    }
  }
  &.top {
    top: -300px;
    &.open-side {
      top: 0;
    }
  }
  &.bottom {
    bottom: -300px;
    &.open-side {
      bottom: 0;
    }
  }
  .setting-block {
    padding: 0 30px;
    h5 {
      text-transform: uppercase;
      margin-bottom: 10px ;
      font-size: 18px;
      color: $font-color;
    }
    ul {
      li {
        display: block;
        margin-bottom:5px;
        a {
          color: $font-color;
          font-size: 16px ;
          text-transform: capitalize;
          &:hover {
            color: $theme-color1 ;
          }
        }
      }
    }
  }
}


/*=====================
  27.add to account bar start
==========================*/
.account-bar  {
  .theme-form {
    padding-left: 20px;
    padding-right: 20px;
    .form-control {
      border-radius: 20px;
    }
    .forget-class {
      a {
        color: $theme-color1;
        text-transform: capitalize;
        margin-top: 10;
      }
    }
    h6 {
      margin-top: 10px;
    }
  }
}

/*=====================
  28. Tab to top CSS start
==========================*/
.tap-top {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  position: fixed;
  bottom: 100px;
  right: 30px;
  z-index: 8;
  display: none;
  color: $white;
  text-align: center;
  background: $theme-color1;
  border: none;
  font-size: 22px;
  padding: 7px;
  cursor: pointer;
  &.top-cls {
    bottom: 130px;
  }
  &:hover {
    i {
      animation: fadeInUp  500ms ease-in-out;
    }
  }
}

/*=====================
   29. Modal CSS start
==========================*/
.theme-modal {
  padding-right: 0 !important;
  .quick-view-img{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .modal-dialog {
    .modal-content {
      .modal-body {
        background-color: white;
        border-radius:5px;
        .modal-bg {
          background-color: $white;
          padding: 25px;
          .close {
            padding-right: 10px;
            padding-top: 5px;
            position: absolute;
            right: 17px;
            top: 0;
            span {
              color: $black;
            }
          }
          .age-content {
            h2 {
              border-bottom: 1px dashed $gray;
              text-align: center;
              margin-bottom: 15px;
              padding-bottom: 15px;
              font-weight: 700;
              font-size: 24px;
            }
            h4 {
              margin-bottom: 30px;
              text-align: center;
              color: #272727;
              font-weight: 400;
              text-transform: capitalize;
              line-height: 1.6;
              font-size: 14px;
            }
            form {
              margin: 0 auto;
              text-align: center;
              >div {
                display: flex;
              }
              .form-control {
                padding-top: 15px;
                padding-bottom: 15px;
                text-align: center;
                font-size: 12px;
                height: 45px;
                border-radius: 0;
                width: 100%;
                letter-spacing: 0.05em;
                margin: 0 10px;
              }
            }
          }
          .offer-content {
            img {
              margin-bottom: 40px;
            }
            h2 {
              margin-bottom: 15px;
              text-align: center;
              color: $font-color;
              text-transform: uppercase;
              line-height: 1;
              font-weight: 700;
              letter-spacing: 0.05em;
            }
            form {
              .form-group {
                margin: 0 auto;
                text-align: center;
                .form-control {
                  padding-top: 15px;
                  padding-bottom: 15px;
                  text-align: center;
                  font-size: 14px;
                  max-width: 640px;
                  left: 0;
                  right: 0;
                  margin: 0 auto;
                  letter-spacing: 0.05em;
                  margin-bottom: 15px;
                  border-radius: 0 !important;
                }
                .btn-normal{
                  text-transform: uppercase;
                }
              }
            }
          }
        }
      }
    }
    .quick-view-modal,
    .demo_modal {
      button {
        &.close {
          position: absolute;
          z-index: 1;
          right: 10px;
          top: 5px;
        }
      }
      .view-detail {
        font-size: 14px;
        font-weight: 700;
        text-transform: uppercase;
      }
    }
  }
  &.demo-modal {
    .modal-dialog {
      .modal-content {
        .modal-body {
          height: 75vh;
          overflow-y: scroll;
          padding: 25px;
          background-image: none !important;
          &::-webkit-scrollbar {
            width: 5px;
          }
          &::-webkit-scrollbar-track {
            background: #f1f1f1;
          }
          &::-webkit-scrollbar-thumb {
            background: $theme-color1;
          }
          &::-webkit-scrollbar-thumb:hover {
            background: #555;
          }
        }
      }
    }
  }
  &.cart-modal {
    .modal-dialog {
      .modal-content {
        .modal-body {
          background: url("../images/cart-back.png"), $light-border;
          background-position: center;
          background-size: cover;
          background-blend-mode: overlay;
          .modal-bg {
            background-color: transparent;
            &.addtocart {
              .media {
                a {
                  img {
                    max-width: 150px;
                  }
                }
                .media-body {
                  padding: 0 15px;
                  a {
                    h6 {
                      font-size: 16px;
                      color: $theme-color1;
                      text-transform: capitalize;
                      margin-bottom: 20px;
                      i {
                        margin-right: 8px;
                        color: $white;
                        background: #19a340;
                        font-size: 14px;
                        width: 18px;
                        height: 18px;
                        line-height: 18px;
                        border-radius: 50%;
                      }
                    }
                  }
                  .buttons {
                    margin-bottom: 20px;
                    a {
                      display: inline-block;
                      font-size: 16px;
                      padding: 6px 10px;
                      margin: 0 6px;
                    }
                  }
                }
              }
              #upsell_product {
                .product-box {
                  padding-left: 10px;
                  padding-right: 10px;
                  .img-wrapper {
                    box-shadow: 0 0 5px rgba($light-border ,0.6);
                  }
                  .product-detail {
                    text-align: center;
                    padding: 10px;
                    h6 {
                      font-weight: 700;
                      text-transform: capitalize;
                      a {
                        color:$theme-color1;
                      }
                    }
                  }
                }
              }
            }
          }
          .product-upsell {
            margin: 20px 0 10px;
            h4 {
              font-weight: 600;
              margin-bottom: 20px;
            }
          }
        }
      }
    }

  }
  &#exampleModal {
    .modal-body{
      padding: 0;
    }
    .close {
      span {
        color: $theme-color1;
        font-size: 35px;
        text-shadow: none;
      }
    }
    .modal-bg{
      background-color: #ffffff96 !important;
      padding: 0;
    }
    .close{
      position: absolute;
      right: 10px;
    }
    .offer-content{
      background-image: url(../images/bg-newslatter.jpg);
      background-repeat: no-repeat;
      background-position: right center;
      background-color: rgba(255, 255, 255, 0.4);
      min-height: 460px;
      display: flex;
      align-items: center;
      padding-right: 50%;
      &.vagi-offer-contant {
        background-image: url(../images/modal-popup/Vegetable.jpg);
      }
      > div {
        width: 100%;
        padding: 50px;
      }
      p{
        text-align: center;
        color: $icon-color;
        line-height: 1.6;
        margin-bottom: 20px;
      }
    }
  }
  &.exit-modal {
    .modal-body{
      background-image: url(../images/bg-popup.png) !important;
      background-size: cover !important;
      background-position: center !important;
    }
    .modal-bg{
      background-color: #ffffffb3 !important;
    }
    .modal-content{
      border-radius:10px;
    }
    .media {
      .media-body {
        h4 {
          font-size: 16px;
          line-height: 1.2;
          color: $font-color;
          font-weight: 500;
          b{
            color: $theme-color1;
          }
        }
        h5 {
          font-size: 18px;
          color: $gray;
          margin-bottom: 0;
          padding-bottom: 0;
        }
      }
    }
  }
  &#quick-view{
    .modal-body{
      padding:0;
    }
  }
  .product-right{
    .qty-box {
      .input-group{
        button{
          padding-left: 10px;
        }
      }
    }
    input{
      padding: 4px
    }
    h2{
      margin-top: 15px;

    }

    .product-buttons{
      .btn-normal {
        padding: 11px 20px;
      }
    }
  }
}

/*=====================
  30. Loader css start
==========================*/
.loader-wrapper {
  background-color: $white;
  margin: 0;
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 99;
