/*=====================
 31. masonory-banner css start
==========================*/
.masonory-banner {
  .masonary-banner-main {
    position: relative;
    .masonary-banner-contant {
      position: absolute;
      top: 0;
      left: 0;
      width:100% ;
      height: 100% ;
      display: flex;
      align-items: flex-end;
      text-align: center ;
      padding: 80px 0;
      h2 {
        color: $theme-color2 ;
        text-transform: uppercase;
        margin-bottom: 15px;
      }
      h5 {
        color: $theme-color2;
        text-transform: uppercase;
        letter-spacing: 7px;
        background-color: $white;
        padding: 10px 20px;
        font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (1920 - 320)));
      }
      .btn-rounded {
        text-transform: uppercase;
        margin-top: 25px;
        &:hover {
          background-color: $theme-color2;
        }
      }
      .masonary-banner-subcontant {
        margin-right: 70px;
        height: 100% ;
        align-items: center ;
        display: flex;
      }
      &.p-left {
        justify-content: flex-start;
      }
      &.p-center {
        justify-content: center;
      }
      &.p-right {
        justify-content: flex-end;
      }
    }
    .masonory-banner-img {
      height: 450px;
      &.masonory-img1 {
        height: 915px;
      }
    }
  }
}
.masonary-banner-block {
  >div {
    &:first-child {
      margin-bottom: 15px;
    }
  }
}