        }
      }
    }
  }
  .product-form-box {
    .timer {
      span {
        min-width: 45px;
        .padding-l {
          padding-left: 9px;
        }
      }
    }
    .product-buttons {
      .btn-solid, .btn-outline {
        padding: 5px 5px;
      }
    }
  }
  .product-form-box, .border-product {
    .timer {
      span {
        min-width: 45px;
        .padding-l {
          padding-right: 5px;
        }
      }
    }
  }
  // tools //
  .tools-parallax-product  {
    .tools-description {
      .tools-form {
        .search-box {
          width: 92%;
        }
      }
    }
  }

  // vagitabl //
  .full-box {
    .center-slider {
      .offer-slider {
        .product-box {
          display: flex;
          border-bottom: 1px solid $light-border;
          border-top: 1px solid $light-border;
          .img-wrapper {
            img {
              height: 120px;
            }
          }
          .product-info {
            text-align: left;
            h6 {
              padding-top: 5px;
            }
            .color-variant {
              padding-top: 2px;
              li {
                height: 15px;
                width: 15px;
                padding-right: 3px;
              }
            }
          }
          .product-detail {
            padding-left: 0;
            h4 {
              font-size: 20px;
            }
            .color-variant {
              padding-top: 0;
            }
          }
          &:hover {
            .product-info {
              .btn-outline {
                display: none;
              }
              .color-variant {
                opacity: 1;
              }
            }
          }
        }
      }
    }
    .theme-card {
      .offer-slider {
        .sec-1 {
          display: inline;
          .product-box2 {
            border-bottom: 1px solid $light-border;
            &:last-child {
              border-bottom: none;
            }
            + .product-box2 {
              border-left: none;
            }
          }
        }
      }
    }
  }
  .center-slider {
    .offer-slider {
      .product-box {
        .product-detail {
          text-align: left !important;
          .rating {
            i {
              padding-right: 3px;
            }
          }
          h4 {
            padding-bottom: 0.5rem;
          }
          .color-variant {
            li {
              height: 18px;
              width: 18px;
              padding-right: 3px;
            }
          }
        }
        .img-wrapper {
          .cart-box {
            display: none;
          }
        }
      }
    }
  }
}
@media(max-width:320px){
  // rounded category //
  .rounded-category  {
    .category-contain  {
      .img-wrapper {
        width: 70px;
        height: 70px;
      }
    }
  }



  //deal banner //
  .deal-banner {
    .deal-banner-containe {
      h1 {
        line-height: 1.4;
      }
    }
  }
}
@media (min-width: 768px) {
  .p-r-md-0{
    padding-right: 0;
  }
  .p-l-md-0 {
    padding-left: 0;
  }

}
